*{
  font-family: 'Chilanka', cursive;
  box-sizing: border-box;
  
}
html{
  background: rgba(1,6,10,1);
  

}

body{
 
  margin-top: 10px;
}


h2{
  text-align: center;
  color: rgb(141, 212, 212);

  animation: slideInLeft; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 2s; /* don't forget to set a duration! */
}

h4{
 
  animation: slideInRight; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 2s; /* don't forget to set a duration! */
  color: rgb(141, 212, 212);
  margin: 1px;
  padding: 0;

}


.formulario{
  text-align: center;  
}

input{
  width: 50%;
  height: 40px;  
  font-size: 20px;
  box-sizing: border-box;
  border-radius: 5px;
  outline: none;
  border: 0;

  animation: bounceIn; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 2s; /* don't forget to set a duration! */
  


}


.container{
  display: grid;
  grid-template-columns:  1fr;
  grid-template-rows: 130px 1fr 30px;
  grid-template-areas: "header "
                       "content "
                       "footer"
  ;
  grid-gap: 10px; 
  overflow: hidden;
}




.header{
  grid-area: header;
  background: rgba(1,6,10,1);
}

.content{
  grid-area: content;  

  background: rgba(1,6,10,1);
  background: -moz-linear-gradient(top, rgba(1,6,10,1) 0%, rgba(32,124,229,1) 81%, rgba(32,124,229,1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(1,6,10,1)), color-stop(81%, rgba(32,124,229,1)), color-stop(100%, rgba(32,124,229,1)));
  background: -webkit-linear-gradient(top, rgba(1,6,10,1) 0%, rgba(32,124,229,1) 81%, rgba(32,124,229,1) 100%);
  background: -o-linear-gradient(top, rgba(1,6,10,1) 0%, rgba(32,124,229,1) 81%, rgba(32,124,229,1) 100%);
  background: -ms-linear-gradient(top, rgba(1,6,10,1) 0%, rgba(32,124,229,1) 81%, rgba(32,124,229,1) 100%);
  background: linear-gradient(to bottom, rgba(1,6,10,1) 0%, rgba(32,124,229,1) 81%, rgba(32,124,229,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#01060a', endColorstr='#207ce5', GradientType=0 );
  
  
  border-radius: 6px;
  overflow-x: hidden;
}



.footer{
  grid-area: footer; 
  text-align: center; 
}

.alineaciones{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap; 
  text-align: center;
  justify-content: center;   
}

.card{
  max-width: 174px;
  min-width: 174px;
  height: 300px;
  margin: 10px;
  padding: 0;
  overflow: hidden;
  cursor: pointer;
  background: rgb(190, 212, 216);
  border: 0;
  border-radius: 30px;
  margin-bottom: 30px;

  animation: bounceInLeft  ; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 1s; /* don't forget to set a duration! */
  
}

.card img{
  border-start-start-radius: 30px;
  width: 100%;
  height: 200px;
}
.card h3{
  text-align: center;
  vertical-align: center;
}


/* SCROLL */

body::-webkit-scrollbar {
  width: 5px;
}
body::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
body::-webkit-scrollbar-thumb {
  background-color: #155c80;
  border-radius: 10px;
}


.content::-webkit-scrollbar {
  width: 5px;
}
.content::-webkit-scrollbar-track {
  background-color: rgba(5, 5, 5, 0.61);
  border-radius: 5px;
}
.content::-webkit-scrollbar-thumb {
  background-color: #07fdf1;
  border-radius: 5px;
}


@media ( max-width: 400px){
  input{
    width: 90%;
    height: 30px;  
    font-size: 15px;
    
  }


  .card{
    min-width: 300px;  
    height: 320px;   
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
    
  }

  .card:hover { 
    background: rgb(20, 43, 70); 
    color: azure;   
   
  } 

  .card img{
    height: 250px;
  }
  .scrollTop{
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
  }

}


/* top button */




.scrollTop {
  
  position: fixed; 
  width: 100%;
  bottom: 60px;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.5;
  
}

.scrollTop:hover{
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}